import { useMutation as useTSMutation } from '@tanstack/react-query';
import { isAnimaSDKError } from '../errors';
import { queryClient } from '../provider';
import { getAnimaSDK } from '../sdk';
import { MAX_RETRIES } from './constants';

export type UseMutationResult = {
  create: (data: any) => Promise<any>;
  update: (id: string, data: any) => Promise<any>;
  remove: (id: string) => Promise<any>;
  error: Error | null;
  isPending: boolean;
};

export function useMutation(entityName: string): UseMutationResult {
  const mutation = useTSMutation({
    mutationFn: async (action: MutationAction) => {
      const sdk = await getAnimaSDK();

      const entityDb = sdk.db[entityName];
      if (!entityDb) {
        throw new Error(`Entity "${entityName}" not found in the SDK.`);
      }

      switch (action.type) {
        case 'create': {
          return entityDb.create(action.data);
        }
        case 'update': {
          return entityDb.updateById(action.id, action.data);
        }
        case 'remove': {
          return entityDb.deleteById(action.id);
        }
      }
    },
    onSuccess: async () => {
      await queryClient.invalidateQueries({ queryKey: [entityName] });
    },
    retry: (failureCount, error) => {
      if (isAnimaSDKError(error)) {
        // Anima errors are typically non-recoverable
        return false;
      }

      return failureCount < MAX_RETRIES;
    },
  });

  const create = async (data: any) => {
    return mutation.mutateAsync({ type: 'create', data });
  };

  const update = async (id: string, data: any) => {
    return mutation.mutateAsync({ type: 'update', id, data });
  };

  const remove = async (id: string) => {
    return mutation.mutateAsync({ type: 'remove', id });
  };

  return {
    create,
    update,
    remove,
    error: mutation.error,
    isPending: mutation.isPending,
  };
}

type MutationAction =
  | {
      type: 'create';
      data: any;
    }
  | {
      type: 'update';
      id: string;
      data: any;
    }
  | {
      type: 'remove';
      id: string;
    };
