import { useCallback, useEffect } from 'react';
import type { AuthTheme, User } from '@animaapp/playground-sdk';
import { queryClient } from '../provider';
import { getAnimaSDK } from '../sdk';
import { useUser } from './use-user';

export type UseAuthParams = {
  requireAuth?: boolean;
  theme?: AuthTheme;
};

export type UseAuthResult = {
  // Returns the user data if successful, or null if not logged in.
  // If the data is still being fetched, user will be undefined.
  user: User | null | undefined;
  error: Error | null;
  isPending: boolean;
  isAnonymous: boolean;

  login: () => Promise<void>;
  logout: () => Promise<void>;
};

export function useAuth(params?: UseAuthParams): UseAuthResult {
  const { user, error, isPending } = useUser();

  useEffect(() => {
    if (!params?.requireAuth) {
      return;
    }

    const abortController = new AbortController();

    async function requireAuth() {
      const sdk = await getAnimaSDK();

      if (abortController.signal.aborted) {
        return;
      }

      if (user === null) {
        console.warn('User is not authenticated, but authentication is required');
        sdk.auth.login(params?.theme);
      }
    }

    requireAuth();

    return () => {
      abortController.abort();
    };
  }, [user, JSON.stringify(params)]);

  const login = useCallback(async () => {
    const sdk = await getAnimaSDK();
    await sdk.auth.login(params?.theme);
  }, [JSON.stringify(params)]);

  const logout = useCallback(async () => {
    const sdk = await getAnimaSDK();

    await sdk.auth.logout();
    await queryClient.invalidateQueries({ queryKey: ['user'] });
  }, []);

  return {
    user,
    error,
    isPending,
    isAnonymous: user === null,
    login,
    logout,
  };
}
