import { AnimaSDKNotInitializedError } from './errors';

// Timeout after which we give up waiting for the Anima SDK to load, and throw an exception instead.
const TIMEOUT_MS = 5000;

let animaSDKPromise: Promise<typeof window.anima> | null = null;

/**
 * Attempt to get a reference to the Anima SDK, waiting for it if necessary.
 * This "lazy loading" is necessary because in certain environments, the SDK script
 * may be loaded asynchronously after React has already initialized.
 */
export function getAnimaSDK(): Promise<typeof window.anima> {
  if (window.anima) {
    return Promise.resolve(window.anima);
  }
  if (animaSDKPromise) {
    return animaSDKPromise;
  }
  animaSDKPromise = new Promise((resolve, reject) => {
    const start = Date.now();
    const poll = () => {
      if (window.anima) {
        resolve(window.anima);
        animaSDKPromise = null; // Reset for future calls after SDK is loaded
        return;
      }
      if (Date.now() - start >= TIMEOUT_MS) {
        reject(new AnimaSDKNotInitializedError());
        animaSDKPromise = null; // Reset for future calls
        return;
      }
      setTimeout(poll, 50);
    };
    poll();
  });
  return animaSDKPromise;
}
