import type { Filters } from '@animaapp/playground-backend-shared-types';
import { isAnimaSDKError } from '../errors';
import { queryClient } from '../provider';
import { getAnimaSDK } from '../sdk';
import { MAX_RETRIES } from './constants';

export type UseLazyQueryParams = string | Filters;

export type UseLazyQueryResult = {
  query: (params?: UseLazyQueryParams) => Promise<any>;
};

export function useLazyQuery(entityName: string): UseLazyQueryResult {
  const query = async (params?: UseLazyQueryParams) => {
    const { id: entityId, filters } = unpackParams(params);

    return queryClient.fetchQuery({
      queryKey: [entityName, entityId, filters],
      queryFn: async () => {
        const sdk = await getAnimaSDK();

        const entityDb = sdk.db[entityName];
        if (!entityDb) {
          throw new Error(`Entity "${entityName}" not found in the SDK.`);
        }

        if (entityId) {
          return entityDb.findById(entityId);
        } else {
          return entityDb.findMany(params);
        }
      },
      retry: (failureCount, error) => {
        if (isAnimaSDKError(error)) {
          // Anima errors are typically non-recoverable
          return false;
        }

        return failureCount < MAX_RETRIES;
      },
    });
  };

  return {
    query,
  };
}

function unpackParams(params?: UseLazyQueryParams): {
  id: string | undefined;
  filters: Filters | undefined;
} {
  if (typeof params === 'string') {
    return { id: params, filters: undefined };
  }

  return { id: undefined, filters: params };
}
