export class AnimaSDKNotInitializedError extends Error {
  constructor(message: string = 'Anima SDK is not initialized') {
    super(message);
    this.name = 'AnimaSDKNotInitializedError';
  }
}

// We can't check directly with instanceof because the SDK is injected at runtime,
// and not imported as dependency.
// For this reason, we use some heuristics to determine if the error is an AnimaError from the SDK.
export function isAnimaSDKError(error: any): error is Error {
  if (!(error instanceof Error)) {
    return false;
  }

  if (error.name === 'AnimaError') {
    return true;
  }

  if (Object.prototype.hasOwnProperty.call(error, '__ANIMA_SDK_ERROR')) {
    return true;
  }

  return false;
}
