import { useQuery as useTSQuery } from '@tanstack/react-query';
import type { User } from '@animaapp/playground-sdk';
import { isAnimaSDKError } from '../errors';
import { getAnimaSDK } from '../sdk';
import { MAX_RETRIES } from './constants';

export type UseUserResult = {
  // Returns the user data if successful, or null if not logged in.
  // If the data is still being fetched, user will be undefined.
  user: User | null | undefined;
  error: Error | null;
  isPending: boolean;
};

export function useUser(): UseUserResult {
  const { data, error, isPending } = useTSQuery({
    queryKey: ['user'],
    queryFn: async () => {
      const sdk = await getAnimaSDK();
      return sdk.user.me();
    },
    retry: (failureCount, error) => {
      if (isAnimaSDKError(error)) {
        // Anima errors are typically non-recoverable
        return false;
      }

      return failureCount < MAX_RETRIES;
    },
  });

  return {
    user: data,
    error: error,
    isPending,
  };
}
